import React, { Component } from 'react';
import Added from './AddedData'
import updateHandle from './index'

const DiaryContainerStyle = `
    position: initial;
    top: initial;
    left: initial;
    width: 100%;
    margin: auto;
    overflow-y: initial;
    max-height: initial;
    text-align: center;
`
const sumStyle = `
    margin: auto;
    margin-bottom: 10px;
    margin-top: 10px;
    display: inline-block;
`
var changeStyle = () => {
    if (window.innerWidth < 600) {
        document.getElementById('diaryContainerPc').style= DiaryContainerStyle;
        document.getElementById('sumPc').style= sumStyle;
    }
}
class English extends Component {

    state = {phone: false, grams: 100}

    static getDerivedStateFromProps(props, state) {
        let isPhone = window.innerWidth < 600? true : false;
        return {phone: isPhone };
    }
        
    gramHandle = (event) => {
        if (event.target.value < 1) {
            this.setState({grams: event.target.min});
        } else if (event.target.value > 10000) {
            this.setState({grams: event.target.max});
        } else {
            this.setState({grams: event.target.value});
        }
    }

    calc = x => ((x * this.state.grams) / 100).toFixed(1);
    calcKcal = x => ((x * this.state.grams) / 100).toFixed(0);

    PhoneView() {
        return(
            <div className='cardPhone' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                Added.push({name: this.props.name, gram: this.state.grams, cal: this.calcKcal(this.props.cal)})
                                updateHandle();
                                changeStyle();
                            }}>add
                </button>

                <div className="kcalDetailsPhoneEn"><h3>
                    Fat: <p>{this.calc(this.props.fat)} g</p>
                    <br />Carbohydrate: <p>{this.calc(this.props.carb)} g</p>
                    <br />Dietary fiber: <p>{this.calc(this.props.fiber)} g</p>
                    <br />Sugar: <p>{this.calc(this.props.sugar)} g</p>
                    <br />Protein: <p>{this.calc(this.props.protein)} g</p>
                    </h3>
                </div>
                <div className='titlePhoneEn'>
                    <h2>{this.props.name}</h2><br></br>
                    <h5>Calories: {this.calcKcal(this.props.cal)} kcal</h5><br></br>
                    <h6>Per <span>{this.state.grams}</span> <input id="gram" type="number" placeholder={this.state.grams} min='1' max='10000' onChange={this.gramHandle} /> grams</h6>
                </div>
            </div>
        )
    }

    PcView() {
        return( 
            <div className='cardPc' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                Added.push({name: this.props.name, gram: this.state.grams, cal: this.calcKcal(this.props.cal)})
                                updateHandle();
                                var y = Added.map(o => Number(o.cal));
                                if (y.length !== 0) {document.getElementById('cardsContainer').style.marginLeft='160px'}
                            }}>add
                </button>

                <div className="kcalDetailsPcEn"><h3>
                    Fat: <p>{this.calc(this.props.fat)} g</p>
                    <br></br>Carbohydrate: <p>{this.calc(this.props.carb)} g</p>
                    <br></br>Dietary fiber: <p>{this.calc(this.props.fiber)} g</p>
                    <br></br>Sugar: <p>{this.calc(this.props.sugar)} g</p>
                    <br></br>Protein: <p>{this.calc(this.props.protein)} g</p>
                    </h3>
                </div>
                <div className='titlePcEn'>
                    
                    <h4>{this.props.name}</h4><br></br>
                    <h5>Calories: {this.calcKcal(this.props.cal)} kcal</h5>
                    <h6>Per <span>{this.state.grams}</span> <input id="gram" type="number" placeholder={this.state.grams} min='1' max='10000' onChange={this.gramHandle} /> grams</h6>
                </div>
            </div>
        )
    }

    render() {      
        if(this.state.phone) {
            return(
                this.PhoneView()
            )
        } else {
            return(
                this.PcView() 
            )
        }
    }
}

export default English